import React from 'react';

import {
  AppBar, Toolbar,
  Tabs, Tab,
  IconButton
} from '@material-ui/core';
import './navigation.css';

import socialButtons from './socialButtons.js';


class NavigationBar extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      width: null
    }
  }

  updateDimensions() {
    this.setState({width: window.innerWidth});
  }

  componentDidMount() {
    this.updateDimensions();
    window.addEventListener('resize', this.updateDimensions.bind(this));
  }

  renderTabs() {
    return (
      this.props.tabs.map(name =>
	<Tab key={name} label={name}/>
      )
    );
  }

  handleTabChange(event, value) {
    this.props.onTabChange(value);
  }

  renderSocialButtons() {
    const buttons = [];
    for (const [key, data] of Object.entries(socialButtons)) {
      buttons.push(
	<a key={key} href={data.url}>
	  <IconButton className='social-button'>
	    <img
	      className='social-icon'
	      src={data.icon}
	      alt={key + ' social media icon'}
	    />
	  </IconButton>
	</a>
      );
    }
    return buttons;
  }

  render() {
    const logo = (
      <div id='main-logo-container'>
	<a href='/' id='main-logo'>
	  Samuel J. Li
	</a>
      </div>
    );
    const socialButtons = (
      <div id='social-buttons'>
	{this.renderSocialButtons()}
      </div>
    );

    if (this.state.width > 480) {
      return (
	<AppBar id='navigation-bar'>
	  <Toolbar>
	    {logo}
	    <div id='navigation-tabs'>
	      <Tabs
		value={this.props.tabIndex}
		onChange={(event, value) => this.handleTabChange(event, value)}
	      >
		{this.renderTabs()}
	      </Tabs>
	    </div>
	    {socialButtons}
	  </Toolbar>
	</AppBar>
      );
    } else {
      return (
	<AppBar id='navigation-bar'>
	  <Toolbar>
	    {logo}
	    {socialButtons}
	  </Toolbar>
	  <Tabs
	    value={this.props.tabIndex}
	    onChange={(event, value) => this.handleTabChange(event, value)}
	    fullWidth
	  >
	    {this.renderTabs()}
	  </Tabs>
	</AppBar>
      );
    }
  }
}

export default NavigationBar;



