import React from 'react';
import './App.css';

import {MuiThemeProvider} from '@material-ui/core/styles';
import theme from './theme.js';

import NavigationBar from './components/NavigationBar/NavigationBar.js';
import Page from './components/Page.js';

//import Home from './pages/Home.js';
import About from './pages/About.js';
import Projects from './pages/Projects/Page.js';


const pages = {
  projects: {name: 'Projects', content: <Projects/>},
  about: {name: 'About', content: <About/>}
};

const order = ['projects', 'about'];

class App extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      tabIndex: 0
    }
  }

  componentDidMount() {
    document.title = 'Samuel J. Li';

    // Load different pages on startup depending on url
    const hash = window.location.hash;
    if (hash !== '') {
      const index = order.indexOf(hash.slice(1));
      if (index !== -1) {
	this.handleTabChange(index);
      }
    }
  }

  handleTabChange(index) {
    this.setState({tabIndex: index});
    window.location.replace(`/#${order[index]}`);
  }

  renderNavigationBar() {
    return (
      <NavigationBar
	tabs={order.map(id => pages[id].name)}
	tabIndex={this.state.tabIndex}
	onTabChange={index => this.handleTabChange(index)}
      />
    );
  }

  renderPage(id) {
    const page = pages[id];
    return (<Page key={page.name}>{page.content}</Page>);
  }

  render() {
    return (
      <MuiThemeProvider theme={theme}>
	<div>
	  {this.renderNavigationBar()}
	  {this.renderPage(order[this.state.tabIndex])}
	</div>
      </MuiThemeProvider>
    );
  }
}

export default App;



